import React, { useState, useEffect, useRef } from "react";
import "react-datepicker/dist/react-datepicker.css";
import MainHeader from "../../components/Users/MainHeader";
import Alertpopup from "../../components/Alertpopup";
import Alertpopupsuccess from "../../components/Alertpopupsuccess";
import AlertpopupsuccessZoom from "../../components/AlertpopupsuccessZoom";
import "bootstrap/dist/css/bootstrap.min.css";
import moment from "moment-timezone";
import { FaEye, FaTrashAlt } from "react-icons/fa";
import {
  Stepblock,
  Titletext,
  Iconblock,
  Dealcontent,
  SectionWrapper,
  Overlay,
  PopupBox,
  CloseBtn,
  Wrapper,
  StyledIcon,
} from "../../components/Styles/RegisterStyles";
import PaymentPopupAcademy from "../../components/Users/PaymentPopupAcademy"; // Adjust path

import { useParams, useNavigate } from "react-router-dom";

import { Building, Clock, Video, Earth } from "lucide-react";
import { Calendar, momentLocalizer } from "react-big-calendar";
import "react-big-calendar/lib/css/react-big-calendar.css";
import ModuleSideNav from "../../components/Users/ModuleSideNav";
import axios from "axios";
export default function ModuleOne() {
  const navigate = useNavigate();
  const [timeZones, setTimeZones] = useState([]);
  const [selectedZone, setSelectedZone] = useState(
    Intl.DateTimeFormat().resolvedOptions().timeZone
  );
  const [errr, seterrr] = useState(false);
  const [showPopup, setShowPopup] = useState(false);
  const [showPopupPay, setShowPopupPay] = useState(false);
  const [formData, setFormData] = useState({ name: "", email: "" });
  const localizer = momentLocalizer(moment);
  const [ClientIP, setClientIP] = useState("");
  const [zoomData, setzoomData] = useState([]);
  const [errorMsg, seterrorMsg] = useState("");
  const [successMsg, setsuccessMsg] = useState("");
  const [moduledata, setmoduledata] = useState("");
  const [checkModulesub, setcheckModulesub] = useState("");
  const { id } = useParams();
  const storedUsername = localStorage.getItem("UserLoginData");
  const userLogin = JSON.parse(storedUsername);
  const [getDataroompay, setgetDataroompay] = useState("");
  const apiURL = "https://blueprintcatalyst.com/api/user/";
  const apiUrlModule = "https://blueprintcatalyst.com/api/admin/module/";
  const [successData, setSuccessData] = useState(null);
  const [selectedMeetings, setSelectedMeetings] = useState([]);
  const [form, setForm] = useState({ name: "", email: "" });
  const [allEvents, setAllEvents] = useState([]);
  document.title = "Module Page";
  useEffect(() => {
    // Get supported time zones dynamically
    if (Intl.supportedValuesOf) {
      const zones = Intl.supportedValuesOf("timeZone");
      setTimeZones(zones);
    }
  }, []);
  const [registeredMeetings, setRegisteredMeetings] = useState([]);

  useEffect(() => {
    fetchMeetings();
  }, [id]);
  const fetchMeetings = async () => {
    try {
      const res = await axios.post(apiURL + "get_combined_zoom_meetings", {
        module_id: id,
        user_id: userLogin.id,
        selectedZone: Intl.DateTimeFormat().resolvedOptions().timeZone, // auto user timezone
      });

      const meetings = res.data.meetings;

      // Optional: Separate or filter
      const allEvents = meetings;
      const registeredEvents = meetings.filter((m) => m.isRegistered);
      setAllEvents(allEvents);
      setRegisteredMeetings(registeredEvents);
      console.log(registeredEvents);
    } catch (err) {
      console.error("Failed to fetch combined meetings", err);
    }
  };

  useEffect(() => {
    // checkemail();
    getDataroompayment();
  }, []);
  const getDataroompayment = async () => {
    let formData = {
      user_id: "",
    };
    try {
      const res = await axios.post(
        apiUrlModule + "getDataroompayment",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.row;

      setgetDataroompay(respo[0].academy_Fee);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const checkemail = async () => {
    let formData = {
      user_id: "",
    };
    try {
      const res = await axios.post(
        apiURL + "sendAlluserReminderZoomLink",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.row;
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  useEffect(() => {
    if (id !== "undefined") {
      checkmodulesubscription();
    }
  }, [id]);
  const checkmodulesubscription = async () => {
    let formdata = {
      id: id,
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(
        apiURL + "checkmodulesubscription",
        formdata,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      if (res.data.results.length > 0) {
        setcheckModulesub("1");
      }
    } catch (err) {}
  };
  useEffect(() => {
    if (id) {
      getmoduledata();
    }
  }, [id]);
  const getmoduledata = async () => {
    let formdata = {
      id: id,
    };
    try {
      const res = await axios.post(apiURL + "selectModule", formdata, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      console.log(res.data.zoomMeetings);
      if (res.data.results.length > 0) {
        checkmodulesubscription();
        setmoduledata(res.data.results[0]);

        setzoomData(res.data.zoomMeetings);
      } else {
        navigate("/dataroom-Duediligence");
      }
    } catch (err) {}
  };

  useEffect(() => {
    const getIP = async () => {
      try {
        const res = await fetch("https://api.ipify.org?format=json");
        const data = await res.json();
        setClientIP(data.ip); // Save this to state
      } catch (error) {
        console.error("Failed to fetch IP", error);
      }
    };

    getIP();
  }, []);

  useEffect(() => {
    // Get supported time zones dynamically
    if (Intl.supportedValuesOf) {
      const zones = Intl.supportedValuesOf("timeZone");
      setTimeZones(zones);
    }
  }, []);

  const now = new Date();
  now.setHours(0, 0, 0, 0); // Set time to midnight for date-only comparison

  const handleSubmitPay = async (e) => {
    e.preventDefault();
    // handle form submission
    var vl = e.target;
    let formData = {
      name: vl.name.value,
      email: vl.email.value,
      cardnumber: vl.cardnumber.value,
      expiry: vl.expiry.value,
      cvv: vl.cvv.value,
      user_id: userLogin.id,
      plan_id: id,
    };
    try {
      const res = await axios.post(apiURL + "usersubscription", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
    setShowPopup(false);
  };
  //Zoom data

  // const convertZoomMeetingsToEvents = (meetings, selectedZone) => {
  //   return meetings.map((meeting) => {
  //     const originalTime = moment.tz(meeting.meeting_date, meeting.timezone);
  //     const localTime = originalTime.clone().tz(selectedZone);
  //     return {
  //       id: meeting.id,
  //       title: `${meeting.topic}`,
  //       start: localTime.toDate(),
  //       end: localTime.clone().add(30, "minutes").toDate(), // assuming 30 min session
  //       allDay: false,
  //       moduleId: meeting.module_id,
  //       originalMeeting: meeting,
  //     };
  //   });
  // };
  const convertZoomMeetingsToEventsMeet = (meetings, selectedZone) => {
    return meetings.map((meeting) => {
      // Parse original meeting time with its timezone
      const mm = meeting.meeting_date;
      const datePart = mm.split("T")[0]; // This gives "2025-07-02"

      var m = datePart + " " + meeting.time + ":00";
      //console.log(m);
      const sourceTime = moment.tz(
        m, // e.g., "2025-07-03 14:59:00"
        "YYYY-MM-DD HH:mm:ss", // format must match
        meeting.timezone || "UTC" // e.g., "America/Toronto"
      );
      console.log(sourceTime);
      // Convert to target timezone (e.g., Asia/Kolkata)
      const localTime = sourceTime.clone().tz(selectedZone);

      // Create start Date object
      const start = new Date(
        localTime.year(),
        localTime.month(),
        localTime.date(),
        localTime.hour(),
        localTime.minute()
      );

      // End time is 30 mins later
      const end = new Date(start.getTime() + 30 * 60 * 1000);

      return {
        id: meeting.id,
        topic: meeting.topic,
        title: `${localTime.format("hh:mm A")} ${meeting.id}`,
        start,
        end,
        allDay: false,
        datee: meeting.meeting_date_time,
        moduleId: meeting.module_id,
        originalMeeting: meeting,
      };
    });
  };

  const today = moment().format("YYYY-MM-DD");
  const [convertedTimeZones, setConvertedTimeZones] = useState([]);
  const prevMeetingRef = useRef(null); // track previous meeting

  useEffect(() => {
    if (zoomData?.length && timeZones?.length) {
      const today = moment().format("YYYY-MM-DD");

      const todayMeeting = zoomData.find(
        (meeting) => moment(meeting.start).format("YYYY-MM-DD") === today
      );

      if (todayMeeting) {
        // Reset previous ref on module change
        const currentKey = `${id}_${todayMeeting.id}_${moment(
          todayMeeting.start
        ).format()}`;

        if (prevMeetingRef.current === currentKey) {
          return; // No change, skip update
        }

        prevMeetingRef.current = currentKey;

        const originalMoment = moment(todayMeeting.start);

        const convertedZones = timeZones.map((tz) => ({
          value: tz,
          label: `${tz} (${originalMoment.clone().tz(tz).format("hh:mm A")})`,
        }));

        setConvertedTimeZones(convertedZones);
      }
    } else {
      setConvertedTimeZones([]); // clear when data is missing
    }
  }, [zoomData, timeZones, id]);

  const handleSelectEvent = (event) => {
    const isRegistered = registeredMeetings.some((m) => m.id === event.id);
    if (isRegistered) return;

    // âœ… Prevent selection of past meetings
    const now = new Date();

    const isSelected = selectedMeetings.find((m) => m.id === event.id);
    if (isSelected) {
      setSelectedMeetings(selectedMeetings.filter((m) => m.id !== event.id));
    } else {
      if (selectedMeetings.length >= 3) {
        seterrorMsg("Only 3 meetings allowed.");
        setTimeout(() => seterrorMsg(""), 2000);
        return;
      }
      setSelectedMeetings([...selectedMeetings, event]);
    }
  };

  const handleSubmit = async () => {
    if (!form.name || !form.email) {
      seterrorMsg("Please enter your name and email.");
      setTimeout(() => {
        seterrorMsg("");
      }, 1200);
      return;
    }
    let formdata = {
      email: form.email,
      name: form.name,
      user_id: userLogin.id,
      selectedMeetings: selectedMeetings.map((m) => m.id),
      ip: ClientIP,
    };
    try {
      const res = await axios.post(apiURL + "register_zoom", formdata);
      console.log(res.data);
      if (res.data.status === "success") {
        setSuccessData(res.data.selectedMeetings);
        setSelectedMeetings([]);
        fetchMeetings();
        getmoduledata();
      } else {
        seterrorMsg(res.data.message);
        // alert(result.message);
      }
      setTimeout(() => {
        setsuccessMsg("");
        seterrorMsg("");
      }, 1200);
    } catch (err) {
      console.error("Error creating zoom meet", err);
      //alert("Error creating zoom meeting.");
    }
  };
  const closemessage = () => {
    setSuccessData(null); // Clear success message

    setTimeout(() => {
      setShowPopup(false);
    }, 5);
  };
  const [ShowPopupp, setShowPopupp] = useState(true);
  useEffect(() => {
    if (selectedMeetings.length > 0 && !successData) {
      setShowPopupp(false);
    } else {
      setForm({ name: "", email: "" });
      setShowPopupp(true);
    }
  }, [selectedMeetings, successData]);
  const openregisterPopup = () => {
    setForm({ name: "", email: "" });
    if (selectedMeetings.length > 0 && !successData) {
      setShowPopup(true);
    } else {
      setShowPopup(false);
    }
  };
  // const allEvents = [...selectedSlots];

  return (
    <>
      <Wrapper>
        <div className="fullpage d-block">
          <MainHeader />

          <SectionWrapper className="d-block py-5">
            <div className="container-lg">
              <div className="row justify-content-center">
                <div className="col-md-3">
                  <ModuleSideNav />
                </div>
                <div className="col-md-9">
                  <form action="">
                    <Stepblock id="step5">
                      <div className="d-flex flex-column gap-5">
                        {errorMsg && (
                          <Alertpopup
                            message={errorMsg}
                            onClose={() => seterrorMsg("")}
                          />
                        )}

                        {successMsg && (
                          <Alertpopupsuccess
                            message={successMsg}
                            onClose={() => setsuccessMsg("")}
                          />
                        )}

                        {successData && (
                          <AlertpopupsuccessZoom
                            onClose={() => closemessage(null)}
                          >
                            <div className="alert alert-success mt-3">
                              <h5>âœ… Registered Successfully!</h5>
                              <p>
                                Your scheduled Zoom meetings are listed below on
                                the calendar:
                              </p>
                              <ul>
                                {successData.map((meeting, i) => (
                                  <li key={i}>
                                    {meeting.title} â€”{" "}
                                    {moment(meeting.start).format(
                                      "DD MMM, hh:mm A"
                                    )}
                                  </li>
                                ))}
                              </ul>
                              <p className="mt-2">
                                You can view and join these sessions directly
                                using the links in your email.
                              </p>
                            </div>
                          </AlertpopupsuccessZoom>
                        )}

                        <Titletext>{moduledata.name}</Titletext>
                        <div className="row gy-3">
                          <div className="col-md-4">
                            <Dealcontent>
                              <div className="d-flex flex-column gap-3">
                                <div className="klogo">
                                  <div className="inlogo fulw">
                                    <img
                                      src="/logos/logoblack.png"
                                      alt="logo"
                                    />
                                  </div>
                                </div>
                                <h3>Keiretsu Forum Conoda</h3>
                                <h4>Deal Screening - 30 minutes</h4>
                              </div>
                              <h6>
                                <StyledIcon>
                                  <Clock />
                                </StyledIcon>
                                30 min
                              </h6>
                              <h6>
                                <StyledIcon>
                                  <Video />
                                </StyledIcon>
                                Web conferencing details provided upon
                                confirmation.
                              </h6>
                              <p>{moduledata.description}</p>
                            </Dealcontent>
                          </div>
                          <div className="col-md-8">
                            <div className="d-flex flex-column gap-2">
                              <label>Select a date and time</label>
                              <Iconblock>
                                <Calendar
                                  localizer={localizer}
                                  events={allEvents}
                                  startAccessor="start"
                                  endAccessor="end"
                                  style={{ height: 600 }}
                                  popup
                                  selectable
                                  defaultView="month"
                                  views={["month"]} // ðŸ‘ˆ this hides all other view buttons
                                  onSelectEvent={handleSelectEvent}
                                  eventPropGetter={(event) => {
                                    const isRegistered =
                                      registeredMeetings.some(
                                        (m) => m.id === event.id
                                      );
                                    const isSelected = selectedMeetings.some(
                                      (m) => m.id === event.id
                                    );

                                    let className = "event-default";
                                    if (isRegistered)
                                      className = "event-registered";
                                    else if (isSelected)
                                      className = "event-selected";

                                    return { className };
                                  }}
                                />

                                <strong>Time Zone</strong>
                                <select
                                  value={selectedZone}
                                  onChange={(e) =>
                                    setSelectedZone(e.target.value)
                                  }
                                >
                                  {convertedTimeZones.map((tz) => (
                                    <option key={tz.value} value={tz.value}>
                                      {tz.label}
                                    </option>
                                  ))}
                                </select>
                              </Iconblock>

                              <button
                                style={{
                                  opacity: ShowPopupp ? 0.5 : 1,
                                  pointerEvents: ShowPopupp ? "none" : "auto",
                                }}
                                className="registerzoom"
                                onClick={() => openregisterPopup()}
                                type="button"
                              >
                                Register For Zoom
                              </button>
                            </div>
                          </div>
                          <div className="col-12">
                            <Iconblock>
                              <Building />
                              <p>{moduledata.textt}</p>
                            </Iconblock>
                          </div>
                        </div>
                      </div>
                    </Stepblock>
                  </form>
                </div>
              </div>
            </div>
          </SectionWrapper>
        </div>

        <Overlay show={showPopup}>
          <PopupBox>
            <CloseBtn onClick={() => setShowPopup(false)}>Ã—</CloseBtn>
            <div className="card p-3 mt-3">
              <h5 className="mb-2">Register Your Email</h5>
              <input
                placeholder="Name"
                value={form.name}
                onChange={(e) => setForm({ ...form, name: e.target.value })}
                className="form-control mb-2"
              />
              <input
                placeholder="Email"
                value={form.email}
                onChange={(e) =>
                  setForm({
                    ...form,
                    email: e.target.value,
                  })
                }
                className="form-control mb-2"
              />
              <button
                type="button"
                className="btn btn-primary"
                onClick={handleSubmit}
              >
                Confirm Registration
              </button>
            </div>
          </PopupBox>
        </Overlay>
      </Wrapper>
      <PaymentPopupAcademy
        moduledata={moduledata}
        paytmmodule={getDataroompay}
        show={showPopupPay}
        onClose={() => setShowPopupPay(false)}
        onSubmit={handleSubmitPay}
      />
    </>
  );
}
